import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../data/dianzi/专业定位与产业（链）发展的相关性、专业定位与区域优势产业、特色产业、战略性新兴产业、支柱产业的相关度、专业定位与高职培养规格的符合度（专业论证结论）.json'
export default class Comp extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        专业定位与产业（链）发展
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData} text='汽车电子技术专业' color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}

